import uniNavBarv from "../../components/uni-nav-bar/uni-nav-bar.vue";
import uploudImags from "../../components/common/uploud-imags.vue";
import uniPopup from "../../components/uni-popup/uni-popup.vue"
let changelook = ['所有人可见','仅自己可见'];

export default {
	components:{
		uniNavBarv,
		uploudImags,
		uniPopup
	},
	data() {
		return {
			isget:false,
			yinshi:"所有人可见",
			text:'', 
			imageList:[],
		}
	},
	// 监听页面返回事件
	onBackPress() {
		if(!this.text && this.imageList.length<1){return}
		if(!this.isget)	{
			this.baocun()
			return true;
		}
	},
	onReady () { 
		// 默认页面加载弹出层
		this.$refs.popup.open() 
	},
	
	methods: {
		// 保存为草稿
		baocun(){
			uni.showModal({
				content: '是否保存为草稿',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log("保存")
					}else{
						console.log("不保存")
					}
					this.isget = true
					uni.navigateBack({
						delta:1
					})
				},
			});
		},
		// 返回
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		// 发布
		submit(){
			console.log('我是发布')
		},
		// 隐私
		changelook(){
			uni.showActionSheet({
				itemList:changelook,
				success:(res)=> {
					this.yinshi = changelook[res.tapIndex]
				}
			})
		},
		

		// 关闭弹出层
		changeg(){
			this.$refs.popup.close() 
		},
		// 
		uploud(arr){
			this.imageList = arr;
		},
		
		
	},
}
